.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  background: white;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  z-index: 1000;
}

.container {
  display: flex;
  padding: 1rem 1.5rem;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
}

.links {
  display: none;
}

.links a {
  margin-left: 1.5rem;
  text-decoration: none;
  color: var(--clr-funct-font);
  transition: color 0.2s ease-in-out;
}

.links a:hover {
  color: #007bff;
}

.burger {
  background: none;
  border: none;
  cursor: pointer;
}

.mobileMenu {
  position: fixed;
  top: 0;
  right: -70%;
  width: 70%;
  height: 100vh;
  background: white;
  box-shadow: -2px 0 5px rgba(0,0,0,0.1);
  display: flex;
  flex-direction: column;
  padding: 2rem;
  transition: right 0.3s ease-in-out;
}

.mobileMenu a {
  margin-bottom: 1.5rem;
  text-decoration: none;
  color: #333;
  font-size: 1.2rem;
  transition: color 0.2s ease-in-out;
}

.mobileMenu a:hover {
  color: #007bff;
}

.mobileMenu.open {
  right: 0;
}

/* Desktop breakpoint */
@media (min-width: 768px) {
  .links {
    display: flex;
  }

  .burger {
    display: none;
  }

  .mobileMenu {
    display: none;
  }
}